<template>
  <div class="section-w">
    <div class="animation-engine-w is--2">
      <div class="wrapper is--blurcolor large-blur">
        <div class="rightengine">
          <Frames
            :class="codeIsRunning ? 'is--deselect': ''"
            :style="codeIsRunning ? 'z-index: -1;': ''"
            :desmosUrls="desmosUrls"
            :desmosId="desmosId"
            :codeIsRunning="codeIsRunning"
            v-on:on-update-stateList="onUpdateStateList"
            ref="framesComp"
          />

          <div class="fullheightline is--thin"></div>

          <div class="canvaswrapper">

            <DesmosExpressionsComp
              :class="codeIsRunning ? 'is--deselect': ''"
              :style="codeIsRunning ? 'z-index: -1;': ''"
              :desmosUrls="desmosUrls"
              ref="desmosExpressions"
            ></DesmosExpressionsComp>

            <div class="little-row2">

              <div class="little-row2">

                <AutosaveComp
                  :class="codeIsRunning ? 'is--deselect': ''"
                  :style="codeIsRunning ? 'z-index: -1;': ''"
                  :stateList="stateList"
                  v-on:on-change-stateList="onUpdateStateList"
                  ref="autosaveComp"
                />

                <RunComp
                  :stateList="stateList"
                  :desmosUrls="desmosUrls"
                  :frameUrl="frameUrl"
                  v-on:on-toggle-run-button="(v) => codeIsRunning = v"
                  v-on:set-actives-again="onSetActivesAgain"
                  ref="runComp"
                />

              </div>

              <FrameUrlComp
                :class="codeIsRunning ? 'is--deselect': ''"
                :style="codeIsRunning ? 'z-index: -1;': ''"
                v-on:on-load-frameIds="onLoadFrameIds"
                v-on:on-change-frameUrl="onChangeFrameUrl"
              />
            </div>

            <div class="complete-row2">
              <DesmosUrlComp
                :class="codeIsRunning ? 'is--deselect': ''"
                :codeIsRunning="codeIsRunning"
                :stateList="stateList"
                v-on:on-delete-desmosUrl="onDeleteDesmosUrl"
                v-on:on-change-desmos-urls="onChangeDesmosUrls"
                v-on:on-change-desmosId="(v) => { desmosId = v; }"
              />

              <DesmosDisplayComp
                v-on:on-set-desmos-display="(v) => desmosGrid = v"
                :desmosGrid="desmosGrid"
                :desmosUrls="desmosUrls"
              />
            </div>

            <DisplayCode
              v-on:on-click-to-desmos="onClickToDesmos"
              :codeIsRunning="codeIsRunning"
              :desmosId="desmosId"
              :activeDesmosIds="activeDesmosIds"
              :desmosUrls="desmosUrls"
              :desmosGrid="desmosGrid"
              @hook:mounted="setStatesFromApi()"
              ref="displayComp"
            />
          </div>
        </div>
      </div>
    </div>
    <div class="wallpaper"></div>
  </div>
</template>
<script>
export default {
  name: "Index",
  data() {
    return {
      stateList: [],
      frameIds: [],
      clickedFrame: "",
      state: "",
      desmosUrls: [],
      desmosId: 1,
      codeIsRunning: false,
      activeStateId: -1,
      activeDesmosIds: [1],
      desmosGrid: 'normal',
      frameUrl: '',
    };
  },
  components: {
    Frames: () => import("../components/frames-comp"),
    DisplayCode: () => import("../components/display-code"),
    FrameUrlComp: () => import("../components/frame-url-comp"),
    DesmosExpressionsComp: () =>
      import("../components/desmos-expressions-comp.vue"),
    AutosaveComp: () => import("../components/autosave-comp"),
    RunComp: () => import("../components/run-comp"),
    DesmosUrlComp: () => import("../components/desmos-url-comp"),
    DesmosDisplayComp: () => import("../components/desmos-display-comp"),
  },
  mounted() {
    // const date = new Date();
    // setInterval(() => {
    //   if (this.frameUrl && this.desmosUrl) {
    //     this.onSendCodeToWebflow (date.getTime())
    //   }
    // }, 5*60*1000);
  },
  methods: {
    onClickToDesmos(e, desmosId) {
      this.$refs.framesComp.setComponentFromDesmos(e, desmosId);
      // to select in desmos expressions component
      this.$refs.desmosExpressions.onSelectExpressionFromDesmos(desmosId);
    },

    // frame url comp
    onLoadFrameIds (frameIds) {
      this.frameIds = frameIds;
      this.$refs.runComp.onResetFrameIds(frameIds);
      this.$refs.framesComp.onResetStateList(frameIds);
    },
    onChangeFrameUrl (frameUrl) {
      this.frameUrl = frameUrl;
      this.$route.query.lobjID = this.frameUrl;
    },

    // frames and autosave
    async onUpdateStateList (stateList, changedWithUndoRedo) {
      this.stateList = stateList;
      await this.$refs.framesComp.onSetStateList(stateList, changedWithUndoRedo)
      // auto save stateList
      if (!changedWithUndoRedo) {
        this.$refs.autosaveComp.onSaveToMemento();
      }
    },

    // run component
    onSetActivesAgain () {
      this.activeStateId = window.activeStateId;
      this.activeDesmosIds = window.activeDesmosIds;
    },

    // desmos url component
    onDeleteDesmosUrl (desmosId) {
      const updatedStateList = this.stateList
      updatedStateList.forEach((usl) => {
        usl.transformation = usl.transformation.filter((st) => {
          return st.desmosId !== desmosId;
        })
      });
      this.onUpdateStateList(updatedStateList)
    },
    onChangeDesmosUrls (value) {
      this.desmosUrls = value;
      this.$route.query.canvasID = this.desmosUrls.join(',');
    },

    // fetch from api
    setStatesFromApi: async function () {
      this.frameUrl = this.$route.query.lobjID;
      const canvases = this.$route.query.canvasID;
      window.desmosUrl = canvases?.split(',')[0];
      this.nameID = this.$route.query?.nameID;
      try {
        if (this.$route.query.id) {
          const newUrl = new URL(`https://x8ki-letl-twmt.n7.xano.io/api:9toeBRNq/states/${this.$route.query.id}`)
          const response = await this.$axios({ method: "get",  url: newUrl.pathname, baseURL: newUrl.origin})
          const currentStates = response.data.states;
          const json = JSON.stringify(response.data.desmos);
          let stateZero;
          if (json) {
            stateZero = JSON.parse(json, (key, val) => (
              !['version', 'xAxisStep', 'yAxisStep', 'xAxisMinorSubdivisions', 'yAxisMinorSubdivisions', 'squareAxes', 'xmax', 'xmin', 'ymax', 'ymin', 'showGrid'].includes(key) &&
              typeof val !== 'object' && typeof val !== 'boolean' && val !== null ? String(val) : val
            ));
            if (stateZero[0]) {
              for (const i in stateZero) {
                await window[`calculator${+i+1}`].setState(stateZero[i])
              }
            } else {
              await window[`calculator${window.desmosId}`].setState(stateZero);
            }
          }
          if (currentStates && currentStates[0] && this.frameIds[0]) {
            this.frameIds.forEach((fi) => {
              if (currentStates.findIndex((s) => s.startFrame === fi) === -1) {
                currentStates.push({
                  startFrame: fi,
                  endFrame: "",
                  type: "enter",
                  transformation: [],
                  showDetails: false,
                });
              }
            });
            await currentStates.forEach((cs) => {
              cs.showDetails = true;
              cs.transformation.forEach((ct) => {
                ct.desmosId = ct.desmosId || 1;
                let itemExpIndex = -1;
                let itemExp;
                if (stateZero[+ct.desmosId-1]) {
                  itemExpIndex = stateZero[+ct.desmosId-1]?.expressions?.list.findIndex((exp) => exp.id === ct.compId);
                  itemExp = stateZero[+ct.desmosId-1]?.expressions?.list[itemExpIndex];
                } else {
                  itemExpIndex = stateZero?.expressions?.list.findIndex((exp) => exp.id === ct.compId);
                  itemExp = stateZero?.expressions?.list[itemExpIndex];
                }
                ct.expidx = +itemExpIndex+1;
                ct.attributeOption = ["Hidden", "Color", "Select", "Custom"]
                if (ct.compId === "desmosCanvas") {
                  ct.attributeOption = ["Math Boundary", "xAxisLabel", "yAxisLabel"];
                } else if (ct.attribute && ct.attribute.slice(0, 4) === "SYNC") {
                  if (!ct.sliderBounds && !ct.slider) ct.sliderBounds = itemExp?.slider || itemExp?.sliderBounds || { min: 0, max: 15, step: .01 };
                  cs.type = "inView";
                  ct.attributeOption = ["SYNC", "SYNC-Custom", "SYNC-C-Backward", "SYNC-Backward", "SYNC-Constant"];
                } else if (ct.attribute === "showLabel" || ct.attribute === "pointActive") {
                  ct.attributeOption = ["Hidden", "Color", "Select", "Custom", "showLabel", "pointActive"];
                }
              })
            });
            await this.$refs.framesComp.onSetStateList(currentStates);
            currentStates.sort((a, b) => {
              if (this.frameIds.indexOf(a.startFrame) < this.frameIds.indexOf(b.startFrame)) {
                return -1
              } else if (this.frameIds.indexOf(a.startFrame) > this.frameIds.indexOf(b.startFrame)) {
                return 1
              }
              return 0
            });
            this.stateList = currentStates;
          }
        }
      } catch (error) {
        console.log(error);
        alert(error);
      }
    },

    // shortcuts
    shortcutFuncs(event) {
      const modifier = event.ctrlKey || event.metaKey;
      if (modifier && event.shiftKey && (event.key === "E" || event.key === "e")) {
        this.$refs.runComp.onClickToRunCode();
      } else if (modifier && event.shiftKey && (event.key === "S" || event.key === "s")) {
        this.$refs.runComp.onSendCodeToWebflow();
      } else if (modifier && event.shiftKey && (event.key === "Z" || event.key === "z")) {
        this.$refs.autosaveComp.redoToMemento();
      } else if (modifier && (event.key === "Z" || event.key === "z")) {
        this.$refs.autosaveComp.undoToMemento();
      }
    },
  },
  created() {
    window.addEventListener("keyup", this.shortcutFuncs);
  },
  destroyed() {
    window.removeEventListener("keyup", this.shortcutFuncs);
  },
};
</script>

<style>
.blur {
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
}
.large-blur {
  -webkit-backdrop-filter: blur(25px);
  backdrop-filter: blur(25px);
}

.complete-row2 {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-gap: 5px;
}

.little-row1 {
  display: grid;
  grid-template-columns: auto;
  grid-gap: 5px;
}
.little-row2 {
  display: grid;
  grid-template-columns: 0.1fr auto;
}
.little-row3 {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 5px;
}
.little-row4 {
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-gap: 5px;
}
.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}
</style>